.mintzy {
    display: flex;
    font-family: 'Gugi', cursive;
    background-color:rgb(24, 24, 24);
    height: 100vh;
    z-index: -8;
    max-width: 100vw;
    background-size: cover;

    background-attachment: fixed;

}
.mintbtn {
    position: relative;
    padding: 10px;
    font-size: 18px;
    height: 40px;
    border: none;
    z-index: 800000;
    background-color: rgb(255, 217, 0);

}
.pic{
    height: 500px;
    padding: 30px;
    
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 1rem;
  width: 100vw;
}

.box {
  display: flex;
  flex-direction: column;

  align-items: center;
  margin: 2rem;
  width: 350px;
  height: 350px;
  border-radius: 8px;
  color: white;

  text-align: left;
  box-shadow: 0 4px 6px rgba(255, 255, 255, 0.1);
  
}


.box1 {
  background-image: url('./king.png');
  transition: all .5s ease-in-out;
}
.box1:hover {
  box-shadow: 0 4px 16px rgba(255, 0, 0, 0.39);
  transform: scale(1.04);
  background-color: rgba(139, 0, 0, 0.185);
}
.box2 {
  background-image: url('./play.png');
  transition: all .5s ease-in-out;
}
.box2:hover {
  box-shadow: 0 4px 16px rgba(0, 255, 34, 0.39);
  transform: scale(1.04);
  background-color: rgba(0, 116, 62, 0.185);
}
.box3 {
  background-image: url('./stake.png');
  transition: all .5s ease-in-out;
}
.box3:hover {
  box-shadow: 0 4px 16px rgba(255, 255, 255, 0.39);
  transform: scale(1.04);
  background-color: rgba(240, 248, 255, 0.185);
}

.box1, .box2, .box3 {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

}


h2 {
  margin-bottom: 13rem;


}

.buttonz {
  background-color: #2bff00;
  border: none;
  color: rgb(0, 0, 0);
  margin-bottom: -15em;
  cursor: pointer;
  padding: 0.5rem 1rem;
  text-transform: uppercase;
  font-size: 14px;
  transition: all ease-in-out .3s;
}

.buttonz:hover {
  background-color: #b30000;
  color: aliceblue;
  transform:scale(1.01);
}

@media screen and (max-width: 768px) {
  .box {
      width: calc(100% - 2rem);
  }
}