@import url(https://fonts.googleapis.com/css2?family=Gugi&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gugi&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: left;
height:100%;

}

.App-logo {

  height: 100px;
  padding-right: 150px;
}

.App-header {
  background-color: #2b2b2b;
  height: 130px;
  color: white;
  display: flex;
  flex-direction:row;
  margin-top: -30px;

  

}

.App-title {
  font-size: 1.3em;
  margin: 0 20px;
  line-height: 1.7rem;
  margin-top: 50px;
  transition: all ease-in-out .5s;
}
.App-title:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  font-size: 1.3em;
  color: red;
  cursor: pointer;
}
.App-intro {
  margin-top: 30px;
  font-size: large;
}



.mintzy {
    display: flex;
    font-family: 'Gugi', cursive;
    background-color:rgb(24, 24, 24);
    height: 100vh;
    z-index: -8;
    max-width: 100vw;
    background-size: cover;

    background-attachment: fixed;

}
.mintbtn {
    position: relative;
    padding: 10px;
    font-size: 18px;
    height: 40px;
    border: none;
    z-index: 800000;
    background-color: rgb(255, 217, 0);

}
.pic{
    height: 500px;
    padding: 30px;
    
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 1rem;
  width: 100vw;
}

.box {
  display: flex;
  flex-direction: column;

  align-items: center;
  margin: 2rem;
  width: 350px;
  height: 350px;
  border-radius: 8px;
  color: white;

  text-align: left;
  box-shadow: 0 4px 6px rgba(255, 255, 255, 0.1);
  
}


.box1 {
  background-image: url(/static/media/king.70da0b67.png);
  transition: all .5s ease-in-out;
}
.box1:hover {
  box-shadow: 0 4px 16px rgba(255, 0, 0, 0.39);
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
  background-color: rgba(139, 0, 0, 0.185);
}
.box2 {
  background-image: url(/static/media/play.78934e93.png);
  transition: all .5s ease-in-out;
}
.box2:hover {
  box-shadow: 0 4px 16px rgba(0, 255, 34, 0.39);
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
  background-color: rgba(0, 116, 62, 0.185);
}
.box3 {
  background-image: url(/static/media/stake.23cf1580.png);
  transition: all .5s ease-in-out;
}
.box3:hover {
  box-shadow: 0 4px 16px rgba(255, 255, 255, 0.39);
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
  background-color: rgba(240, 248, 255, 0.185);
}

.box1, .box2, .box3 {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

}


h2 {
  margin-bottom: 13rem;


}

.buttonz {
  background-color: #2bff00;
  border: none;
  color: rgb(0, 0, 0);
  margin-bottom: -15em;
  cursor: pointer;
  padding: 0.5rem 1rem;
  text-transform: uppercase;
  font-size: 14px;
  transition: all ease-in-out .3s;
}

.buttonz:hover {
  background-color: #b30000;
  color: aliceblue;
  -webkit-transform:scale(1.01);
          transform:scale(1.01);
}

@media screen and (max-width: 768px) {
  .box {
      width: calc(100% - 2rem);
  }
}
.Login-button {
  position: relative;
  display: block;
  justify-content: center;
  align-items: center;

  padding: 10px;
  font-size: 18px;
  
  border: none;
}

.Login-button:hover {
  opacity: 0.9;
}



.Login-mm {
  background-color: rgb(255, 0, 0);
  font-family: 'Gugi', cursive;
  transition: all ease-in .5s;
  position: relative;
  display: block;
  justify-content: center;
  align-items: center;
}

.Login-mm:hover {
  background-color: rgb(34, 170, 0);

}


.page {
  min-height: 110vh;
  margin: 0;
  background-image: url(/static/media/BG.6545346a.jpg);
  background-size: cover;
  background-position: 30% 50%;
  position: absolute;
  color: aliceblue;
  background-color: black;
  font-family: 'Gugi', cursive;
}
.header {
  padding: 150px 0px 0px 100px;
  font-size: 45px;
  max-width: 50vw;
  background-color: rgb(0, 0, 0);
}
.pic {
  justify-content: center;
  width: 26%;
  height: auto;
  display: block;
  margin-left: auto;
  margin-top: auto;
  margin-bottom: auto;
  margin-right: auto;
  width: 50%;


}
.identicon {
  border-radius: 50%;
  vertical-align: middle;
}
.Login-button {
  position: absolute;
  right: 20px;
  top: 30px;
  padding: 10px;
  font-size: 18px;
 
  border: none;
}
.Profile {
  display: flex;
  font-family: 'Gugi', cursive;
  background-image: url(/static/media/banner.a614afff.png);
  height:50vh;
  background-size: cover;
  width:auto ;
  margin-top: -34px;
  background-attachment: relative;

}
.Login-button:hover {
  opacity: 0.9;
}



.Login-mm {
  background-color: rgb(255, 125, 0);
  font-family: 'Gugi', cursive;
}
.names {
  position: absolute;
  right: 120px;
  top:0px;
  padding: 10px;
  font-size: 21px;
  color: aliceblue;
  border: none;
  display: flex;
  justify-content: space-around;
  line-height: 55px;
  width: 40vw;
  text-transform: capitalize;
  font-family: 'Gugi', cursive;
}
.edit {
  right: 0;
  position: absolute;
  display: block;
  font-size: 18px;
  margin-top:20px;
  width:250px;
  color: aliceblue;
  text-align: center;
}

