@import url('https://fonts.googleapis.com/css2?family=Gugi&display=swap');

.Login-button {
  position: relative;
  display: block;
  justify-content: center;
  align-items: center;

  padding: 10px;
  font-size: 18px;
  
  border: none;
}

.Login-button:hover {
  opacity: 0.9;
}



.Login-mm {
  background-color: rgb(255, 0, 0);
  font-family: 'Gugi', cursive;
  transition: all ease-in .5s;
  position: relative;
  display: block;
  justify-content: center;
  align-items: center;
}

.Login-mm:hover {
  background-color: rgb(34, 170, 0);

}


.page {
  min-height: 110vh;
  margin: 0;
  background-image: url(./BG.jpg);
  background-size: cover;
  background-position: 30% 50%;
  position: absolute;
  color: aliceblue;
  background-color: black;
  font-family: 'Gugi', cursive;
}
.header {
  padding: 150px 0px 0px 100px;
  font-size: 45px;
  max-width: 50vw;
  background-color: rgb(0, 0, 0);
}
.pic {
  justify-content: center;
  width: 26%;
  height: auto;
  display: block;
  margin-left: auto;
  margin-top: auto;
  margin-bottom: auto;
  margin-right: auto;
  width: 50%;


}