.App {
  text-align: left;
height:100%;

}

.App-logo {

  height: 100px;
  padding-right: 150px;
}

.App-header {
  background-color: #2b2b2b;
  height: 130px;
  color: white;
  display: flex;
  flex-direction:row;
  margin-top: -30px;

  

}

.App-title {
  font-size: 1.3em;
  margin: 0 20px;
  line-height: 1.7rem;
  margin-top: 50px;
  transition: all ease-in-out .5s;
}
.App-title:hover {
  transform: scale(1.03);
  font-size: 1.3em;
  color: red;
  cursor: pointer;
}
.App-intro {
  margin-top: 30px;
  font-size: large;
}


